@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script|Amatic+SC|Open+Sans+Condensed|Inconsolata);
.about-title-container{
  padding: 0.75em;
  background: #DCB982;
  font-family: 'Amatic SC', serif;
  border-radius: 10px 10px 0 0;
  text-align: center;
}

.about-title {
  font-size: 2em !important;
}

.about-container {
  margin-top: 5em;
}

.inner-about-container{
  border: 1px solid black;
  border-radius: 10px 10px 0 0;
}

.about-description-container {
  background-color: rgb(152,182,161);
  text-align: center;
  min-height: 5em;
  font-family: 'Open Sans Condensed';
}

.about-description {
  margin: 0;
  padding: 1em;
}

@media only screen
  and (min-device-width: 320px)
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2) {

  .about-description{
    font-size: 0.3em;
  }
  .about-title{
    font-size: 0.4em !important;
  }

}

#map-container {
  height: 75vh;
  width: 75%;
  box-shadow: 5px 5px 50px #073E66;
  margin: 5em auto;
}

.main-marker {
  color: 'white';
  background: 'grey';
  padding: '5px 3px';
  display: 'inline-flex';
  text-align: 'center';
  align-items: 'center';
  justify-content: 'center';
  border-radius: '10%';
}

#about-bethlehem {
  margin-top: 10em;
}

@media only screen
  and (min-device-width: 320px)
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2) {
    #about-bethlehem {
      margin-top: 16em;
    }
    #map-container {
      height: 60vh;
    }
}

.about-image {
    border-radius: 25px;
    width: 100%;
    box-shadow: 5px 5px 50px;
}

#about-couple-container {
  margin-top: 2em;
}

.inner-about-container{
    box-shadow: 5px 5px 30px;
}

#bottom-row {
  margin-bottom: 5em;
}

body {
  background-color: #EEEE0;
}

.col-md-3 {
  padding: 0;
}

#content {
  margin-top: 7em;
}

/*/////Header stuff/////*/
.header {
  position:fixed;
  top:0;
  z-index: 100;
  margin: 0;
  background-color: rgba(255,79,0, 0.75);
  padding: 1%;
  width: 100%;
  height:7em;
}

#header-title{
  color: #EEE;
}

.header a{
  font-family: 'Amatic SC', serif;
  font-weight: 900;
  letter-spacing: 8.6px;
  color: black;
  text-decoration: none;
  font-size: 3em;
}

.header * {
  text-align: center;
}

/*///// Carousel fixes /////*/
#scroll-strip {
  height: 0.5em;
  background-color: #ff4f00;
  width: 100%;
  margin: 0px;
}

/*////// Left Nav styles /////*/
.link-item {
  font-family: 'Amatic SC', serif;
  font-size: 3em;
  list-style-type: none;
  text-decoration: none;
  color: #073E66;
  padding-top: 1em;
}
.link-item a {
  color: #073E66;
}

.link-item a:visited{
  color: #073E66;
  text-decoration: none;
}

@media only screen
  and (min-device-width: 320px)
  and (max-device-width: 480px)
  and (-webkit-min-device-pixel-ratio: 2) {
  .header a{
    font-size: 9em !important;
  }

  .header {
    height: 13em;
  }

  #content {
    margin-top: 13em;
  }
  .inner-about-container {
    font-size: 9em;
  }

  .about-title {
    font-size: 1em;
  }

  .link-item {
    font-size: 6em;
  }
}

